import {
  getOListButton,
  getUListButton,
  MobileTaskButton,
  MobileTaskInsertButton,
} from '../components/mobileButtons';

export default function toolbar(listConfig) {
  const listLocaleConfig = listConfig.locale || {};
  return {
    buttons: {
      mobileOList: getOListButton('horiz'),
      mobileUList: getUListButton('horiz'),
      mobileOListBig: getOListButton('big'),
      mobileUListBig: getUListButton('big'),
      mobileTask: MobileTaskButton,
      mobileTaskInsert: MobileTaskInsertButton,
    },
    locale: listLocaleConfig,
    stickerList: true,
  };
}
